import { Modal, Comment, Tag, Input } from "antd";
import { useEffect, useState } from "react";
import { CONTAINERS } from "..";
import { IContainer } from "../types";
import { CommentType, IComment } from "./types";

const LOCATIONS = [
    '',
    ' ',
    ' ',
    '   ',
    ' ',
    '   ',
]

const COMMENTS: IComment[] = [
  {
    type: CommentType.text,
    value: " ,  ",
    user: "",
    date: "13.09.2022",
  },
  {
    type: CommentType.text,
    value: "  ,  - ",
    user: "",
    date: "13.09.2022",
  },
  {
    type: CommentType.way,
    value: " ",
    user: "",
    date: "13.09.2022",
  },
  {
    type: CommentType.text,
    value: "   ",
    user: "",
    date: "13.09.2022",
  },
  {
    type: CommentType.text,
    value: " , ,  ",
    user: "",
    date: "13.09.2022",
  },
  {
    type: CommentType.way,
    value: "",
    user: "",
    date: "10.09.2022",
  },
];

export const ContainerModal = ({
  id,
  open,
  onCancel,
}: {
  id: number | null;
  open: boolean;
  onCancel: () => void;
}) => {
  const [data, setData] = useState<IContainer | null>(null);
  const [input, setInput] = useState("");

  const changeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInput(e.target.value);
  };

  const changeLevel = () => {
    const currentIndex = LOCATIONS.findIndex(location => location === data?.location)
    const newLocation = LOCATIONS[currentIndex + 1]
    const newComment = {
        type: CommentType.way,
        value: newLocation,
        date: '13.09.2022',
        user: ''
    }
    if (data && data?.comments) {
        setData({...data, location: newLocation, comments: [newComment, ...data?.comments]}) 
    }
    setInput('');
  };

  const addComment = () => {
    const newComment = {
        type: CommentType.text,
        value: input,
        date: '13.09.2022',
        user: ''
    }
    if (data && data?.comments) {
        setData({
            ...data,
            comments: [newComment, ...data?.comments]
        })
    }
    setInput("");
  }

  useEffect(() => {
    const result = CONTAINERS.find((cont) => cont.id === id);
    if (result) {
      setData({ ...result, comments: COMMENTS });
    }
  }, [id]);

  if (!data) return null;

  return (
    <Modal title={data?.number} open={open} onOk={onCancel} onCancel={onCancel}>
        <div className="container_desc">
            <p> {data.request},  {data.client}</p>
            <Tag color="processing">{data.location}</Tag>
            <a onClick={changeLevel}>   </a>
        </div>
      <h3> </h3>
      <Input value={input} onChange={changeHandler} onPressEnter={addComment} placeholder=' ...'/>
      {data.comments &&
        data.comments.map((comment) => {
          const content =
            comment.type === CommentType.text ? (
              <p>{comment.value}</p>
            ) : (
              <div>
                   {" "}
                <Tag color="processing">{comment.value}</Tag>
              </div>
            );
          return (
            <Comment
              // actions={actions}
              author={<a>{comment.user}</a>}
              content={content}
              datetime={<span>{comment.date}</span>}
            />
          );
        })}
    </Modal>
  );
};
